const Evento = (props) => {
     const { urlToImage, url, title, description, source, publishedAt } = props.noticia

     let imagen = '';

     if (urlToImage !== '') {
          imagen = <img src={urlToImage} alt={title}
          className="card-img-top" />
     } else {
          imagen = <p className="text-center my-5"> Imagen no disponible </p>
     }
     return (
          <div className="col-md-6 col-12 mb-4">
               <div className="card">
                    <div className="card-image">
                         {imagen}
                    </div>
                    <div className="card-body">
                         <h3 className="card-title">{title}</h3>
                         <h4 className="card-text">{publishedAt}</h4>
                         <p className="card-text">{description}</p>
                         <p className="card-text"><b>{source.name}</b></p>
                         <a href={url} target="_blank" className="btn btn-primary d-block">
                              Leer
                         </a>
                    </div>
               </div>
          </div>
     );
}

export default Noticia;